.contactParentWrapper {
  display: grid;
  grid-template-columns: 66% 1fr;
}

.sideDrawerHeaderWrapper {
  display: flex;
  flex-direction: column;
  position: fixed;
  background: #fff;
  width: 100%;
  padding-top: 18px;
  z-index: 1;
}
.sideDrawerHeadingWrapper {
  display: flex;
  align-items: flex-start;
  column-gap: 24px;
  align-items: flex-end;
  margin: 0 19px;
  padding-top: 18px;
}

.skipBtn {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 15px;
  text-align: right;
  text-decoration-line: underline;
  color: #24272c;
  mix-blend-mode: normal;
  opacity: 0.7;
  text-align: center;
  margin-top: 50px;
}

.skipBtn img {
  vertical-align: middle;
  width: 2%;
  margin-left: 5px;
}

.formContainter {
  padding-top: 127px;
  margin: 0 30px;
  margin-left: 57px;
}

.sideDrawerHeading {
  font-family: "Roboto";
  font-weight: 600;
  font-size: 36px;
  line-height: 42px;
  column-gap: 17px;
  color: #24272c;
}

.sideDrawerSubHeading {
  font-family: "Roboto";
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 28px;
  margin-bottom: 20px;
  color: #282c32;
}
.sideDrawerTitle {
  font-size: 1.5rem;
  font-family: "Roboto";
  font-weight: 500;
  color: #24272c;
}

.secondHalf {
  min-width: 372px;
  height: 100vh;
  overflow-y: scroll;
}

.backArrow {
  position: relative;
}

.firstHalf {
  background-color: #e5e9f2;
  height: 100vh;
  background-size: cover;
  background-image: url("../../../assets/images/back.png");
}

.subContent {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  color: #24272c;
  opacity: 0.7;
}

.viewMorbtn {
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  text-decoration-line: underline;
  color: #656f7a;
  margin-top: 5px;
}

@media (max-width: 992px) {
  .contactParentWrapper {
    display: grid;
    grid-template-columns: none;
    grid-template-rows: 147px 1fr;
    background-color: #f3f7f9;
  }
  .firstHalf {
    height: auto;
    background: none;
    background-color: #f3f7f9;
  }
  .secondHalf {
    background: #ffffff;
    box-shadow: 0px -1px 5px rgba(0, 0, 0, 0.15);
    border-radius: 24px 24px 0px 0px;
    width: auto;
  }
  .sideDrawerWrapper {
    margin-top: 18px;
    margin-left: 20px;
    margin-right: 16px;
    min-height: 60vh;
    width: auto;
  }
  .sideDrawerTitle {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    /* identical to box height, or 117% */

    color: #3b414a;
  }
}

.topHeaderWrapper {
  position: relative;
}

.topHeaderWrapper::after {
  position: absolute;
  content: "";
  border-top: 3px solid #12b0ee;
  width: 100%;
  z-index: 1;
  top: 18px;
}

.topHeaderWrapper div {
  display: table;
  position: relative;
  z-index: 16;
  background: #fff;
  margin-left: 55px;
}

.topHeaderWrapper img {
  width: 36px;
  border: solid 2px #12b0ee;
  padding: 2px;
  border-radius: 5px;
}

.topHeaderWrapper span {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  color: #2e3239;
  mix-blend-mode: normal;
  opacity: 0.7;
  line-height: 39px;
  vertical-align: top;
  padding: 0 15px;
}

.Page_Profile {
  display: grid;
  grid-template-columns: 94% 6%;
  margin: 30px auto;
  align-items: flex-start;
  background: #f6f9fb;
  margin: 0 -30px 0 -57px;
  padding: 30px 57px;
}

.Page_Profile p {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  text-decoration-line: underline;
  color: #24272c;
  margin-top: 20px;
}

.imgContainer {
  background: #ffffff;
  border: 1px solid rgba(36, 39, 44, 0.1);
  border-radius: 8px;
  width: 35px;
  align-items: center;
}

.PageImg {
  width: 100%;
  border-radius: 3px;
}

.Page_Profile > label {
  display: grid;
  grid-template-columns: 12% 88%;
}

.Page_label {
  top: 0;
  transform: none;
  position: relative;
}
.Page_label div {
  margin-top: 0;
}

.Page_Profile h1 {
  font-family: Roboto;
  font-size: 18px;
  font-weight: 500;
  color: #24272c;
  align-self: center;
}

.Page_Profile input {
  width: 22px;
  height: 25px;
}

.UserConfirmation {
  margin-top: 50px;
  grid-template-columns: 10% 90%;
  margin-bottom: 10px;
  margin-left: 0;
}

.UserConfirmation label {
  font-size: 15px;
  color: #2e3239;
}

.UserConfirmation label a {
  color: black;
  text-decoration: underline;
}

.UserConfirmation input {
  margin-right: 10px;
  height: 20px;
  width: 20px;
}

.LinkedIn_FinishBtn {
  display: flex;
  align-items: center;
  border-radius: 6px;
  padding: 12px;
  margin-left: 0;
  margin-top: 25px;
}

.LinkedIn_FinishBtn img {
  width: 30px;
  margin-right: 6px;
}
